/* icons */
.row.icons {
  padding-top: 10px;
  p {
    color: #ffffff;
    font-size: 12px;
    margin-left: 24px;
    margin-top: 14px;
    letter-spacing: 0.03px; }
  .col-lg-4 {
    flex-direction: row;
    text-align: right;
    .icon {
      width: 25px;
      height: 25px;
      display: inline-block;
      margin-right: 25px;
      margin-top: 5px;
      &_4 {
        margin-right: 23px; }
      &_3 {
        margin-right: 23px; }
      &_2 {
        margin-right: 23px; }
      &_1 {
        margin-right: 23px; } } } }
@media screen and (max-width: 991px) {
  .row.icons {
    .col-lg-4 {
      .icon {
        margin-right: 0;
        &_4,
        &_3,
        &_2,
        &_1 {
          margin-right: 21px; } } } } }

@media screen and (max-width: 767px) {
  .row.icons {
    text-align: center;
    .col-lg-3 {
      order: 1; }
    p {
      margin-left: 0; }
    .col-lg-4 {
      text-align: center; } } }
