/* modal */
.modal {
  display: block;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.85);

  .modal_body {
    background: #fff;
    position: fixed;
    top: 50%;
    left: 50%;
    width: 350px;
    height: 230px;
    transform: translateY(-50%) translateX(-50%);
    padding: 2rem 3rem;
    text-align: right;
    h4 {
      text-align: left; }
    p {
      text-align: left;
      margin-bottom: 2rem; } } }

