.about_me {
  padding: 133px 194px 158px;
  background: url('/img/imgs/bg-1.jpg') no-repeat;
  -webkit-background-size: cover;
  background-size: cover;
  h1 {
    color: #fff;
    margin-bottom: 18px; }
  p {
    color: #fff;
    padding: 0 0 0 4px;
    width: 415px;
    letter-spacing: 0.5px;
    word-spacing: 0.6px;
    margin-bottom: 38px; } }


@media screen and (max-width: 1312px) {
  .about_me {
    padding: 133px 100px 158px; } }


@media screen and (max-width: 1150px) {
  .about_me {
    padding: 133px 0px 158px; } }


@media screen and (max-width: 767px) {
  .about_me {
    text-align: center;
    .par1 {
      width: 100%; } } }

