/* header*/
header {
    position: fixed;
    padding: 11.5px 211px 12.5px 195px;
    background: #fff;
    align-items: center;
    display: flex;
    width: 100%;
    height: 64px;
    z-index: 2;
    .logo {
        margin-top: 7px; }
    .nav {
        padding-left: 60px; }
    .button_open {
        position: relative;
        cursor: pointer;
        img {
            position: absolute;
            top: 50%;
            transform: translateY(-50%); } } }



@media screen and (max-width: 1312px) {
    header {
        padding: 11.5px 100px 12.5px; } }

@media screen and (max-width: 1150px) {
    header {
        padding: 11.5px 0px 12.5px; } }
