/* fonts */

@font-face {
  font-family: 'Overpass-SemiBold';
  src: url('./Overpass-SemiBold/Overpass-SemiBold.eot');
  src: url("./Overpass-SemiBold/Overpass-SemiBold.eot");
  src: url("./Overpass-SemiBold/Overpass-SemiBold.eot?#iefix") format("embedded-opentype"),
  url("./Overpass-SemiBold/Overpass-SemiBold.woff") format("woff"),
  url("./Overpass-SemiBold/Overpass-SemiBold.ttf") format("truetype") {}
  font-style: normal;
  font-weight: 600; }

@font-face {
  font-family: 'SourceSansPro-Regular';
  src: url('./SourceSansPro-Regular/SourceSansPro-Regular.eot');
  src: url("./SourceSansPro-Regular/SourceSansPro-Regular.eot");
  src: url("./SourceSansPro-Regular/SourceSansPro-Regular.eot?#iefix") format("embedded-opentype"),
  url("./SourceSansPro-Regular/SourceSansPro-Regular.woff") format("woff"),
  url("./SourceSansPro-Regular/SourceSansPro-Regular.ttf") format("truetype") {}
  font-style: normal;
  font-weight: 400; }

@font-face {
  font-family: 'SourceSansPro-Bold';
  src: url('./SourceSansPro-Bold/SourceSansPro-Bold.eot');
  src: url("./SourceSansPro-Bold/SourceSansPro-Bold.eot");
  src: url("./SourceSansPro-Bold/SourceSansPro-Bold.eot?#iefix") format("embedded-opentype"),
  url("./SourceSansPro-Bold/SourceSansPro-Bold.woff") format("woff"),
  url("./SourceSansPro-Bold/SourceSansPro-Bold.ttf") format("truetype") {}
  font-style: normal;
  font-weight: 700; }


