/*relationships_data */

.relationships {
  h2 {
    padding-left: 3px;
    margin-bottom: 1.5rem; }
  .col-lg-8 {
    padding-right: 0; }
  &_wrap {
    margin-top: 19px; }
  p.par2 {
    margin-bottom: 20px; }
  p.relationships_par_margin {
    margin-bottom: 17px; } }



@media screen and (max-width: 991px) {
  .relationships {
    .col-md-4 {
      img {
        width: 100%; } } } }


@media screen and (max-width: 767px) {
  .relationships {
    &_wrap {
      text-align: center; } } }
