/* general_requirements */
.general_requirements {
  background: #283593;
  padding: 130px 200px 113px;
  h2 {
    padding-left: 3px;
    margin-bottom: 5px;
    color: #fff; }
  &_text_wrap {
    margin-top: 34px;
    padding-left: 10px; }

  p {
    margin-bottom: 22px;
    color: #fff;
    white-space: pre-line; }

  img {
    margin: 48px 0 0 23px; } }

@media screen and (max-width: 1312px) {
  .general_requirements {
    padding: 130px 100px 113px; } }


@media screen and (max-width: 1150px) {
  .general_requirements {
    padding: 130px 0px 113px; } }


@media screen and (max-width: 991px) {
  .general_requirements {
    img {
      width: 100%; } } }


@media screen and  (max-width: 767px) {
  .general_requirements {
    h2 {
      margin-bottom: 30px; }
    .row.flex_order_block {
      .general_requirements_text_wrap {
        order: 1;
        padding-left: 0;
        text-align: center; } }
    img {
      margin: 0; } } }
