.account {
  display: flex;
  align-items: center;
  &_data_wrap {
    text-align: right;
    margin-right: 12px; }
  &_name {
    font-size: 12px;
    font-weight: bold;
    margin-bottom: 0; }
  &_email {
    font-size: 11px;
    color: #CAC9C9;
    margin-bottom: 0; }

  &_avatar {
    width: 40px;
    height: 40px;
    border-radius: 50px;
    margin-right: 13.5px; } }

