/* footer nav */
.footer_nav_wrap {
  padding: 0 0 31px 32px;
  border-bottom: 1px solid #ffffff;
  .col-lg-7 {
    padding-left: 77px;
    .nav_element {
      color: #fff; } } }


@media screen and (max-width: 1199px) {
  .footer_nav_wrap .col-lg-7 {
    padding-left: 0; } }

@media screen and (max-width: 991px) {
  .footer_nav_wrap {}
  .col-lg-7 {
    .nav_element {
      font-size: 15px; } } }

@media screen and (max-width: 767px) {
  .footer_nav_wrap .col-lg-7 {
    flex-direction: column;
    display: flex;
    align-items: center;
    .nav_element {
      margin-right: 0; } } }
