/* sideBar */
.wrap_side_bar {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.85);
  left: -100%;
  top: 0;
  z-index: 11111111;
  display: block;
  transition: .3s;
  &_open {
    left: 0%; }
  .side_bar {
    width: 260px;
    background: #fff;
    height: 100%;
    .account, .links {
      padding: 30px 0 0 30px; }
    .account {
      border-bottom: 1px solid grey;
      padding-bottom: 30px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      img {
        order: 0;
        width: 70px;
        height: 70px;
        margin-bottom: 18px; }
      &_data_wrap {
        order: 1;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        p {
          font-size: 14px; } } }
    .links {
      display: flex;
      flex-direction: column;
      .nav_element {
        margin-bottom: 33px;
        margin-top: 0; } } } }
