/* sign_up */
.sign_up {
  padding: 18px 194px 138px;
  h2 {
    margin-bottom: 12px; }
  p {
    margin-bottom: 60px; }
  form {
    input.input_data {
      outline: none;
      position: relative;
      border: 1px solid grey;
      padding: 14px 97px 15px 16px;
      border-radius: 3px;
      margin-bottom: 40px; }
    span.label {
      position: absolute;
      z-index: 1;
      background: white;
      top: -9px;
      left: 31px;
      font-size: 12px;
      padding: 1px 2px; }
    span.label_1 {
      left: 32px; }
    span.label_2 {
      left: 30px;
      top: -9px; }
    select {
      padding: 16px 298px 15px 16px;
      border-radius: 3px;
      margin-left: 2px;
      color: #EF6C00;
      -webkit-appearance: none;
      position: relative;
      background: rgba(0,0,0,0);
      outline: none; }
    span.after {
      position: absolute;
      height: 20px;
      border-top: 11px solid #000;
      border-left: 11px solid rgba(0, 0, 0, 0);
      border-right: 10px solid rgba(0, 0, 0, 0);
      left: 89%;
      top: 37%;
      border-radius: 3px; }
    input[type="file"] {
      display: none; }
    .custom-file-upload {
      border: 1px solid #EF6C00;
      padding: 13px 33px 12px;
      font-size: 20px;
      font-weight: bold;
      cursor: pointer;
      position: relative;
      span.img_info {
        border: 1px solid grey;
        border-right: none;
        display: block;
        font-weight: 500;
        font-size: 15px;
        position: absolute;
        height: 104%;
        width: 253%;
        right: 100%;
        top: -2%;
        padding: 17px 188px 12px 13px;
        border-radius: 2px;
        color: grey; }
      span.file_info {
        position: absolute;
        font-size: 12px;
        color: grey;
        right: 122%;
        top: 106%;
        width: 231%;
        font-weight: 400; } }
    button {
      margin: 51px 0 0 336px; }
    input.error, span.text-input.error {
      border: red 1px solid; }
    .input-feedback {
      position: absolute;
      bottom: 20%;
      left: 10%;
      color: red;
      font-size: 11px; } } }





@media screen and (min-width: 1370px) {
  .sign_up {
    form {
      .input_feedback_photo {
        left: 20%; } } } }


@media screen and (max-width: 1330px) and (min-width: 1312px) {
  .sign_up {
    .custom-file-upload {
      margin-right: -15px; } } }

@media screen and (max-width: 1312px) and (min-width: 1200px) {
  .sign_up {
    form {}
    .custom-file-upload {
      margin-right:  4%; } } }

@media screen and (max-width: 1312px) {
  .sign_up {
    padding: 18px 100px 138px; } }



@media screen and (max-width: 1150px) {
  .sign_up {
    padding: 18px 0px 138px; } }


@media screen and (max-width: 991px) {
  .sign_up {
    form {
      input.input_data {
        padding-right: 17px; }
      select {
        padding: 16px 179px 15px 16px;
        margin-left: 0; }
      .custom-file-upload {
        span.img_info {
          width: 160%;
          padding: 17px 71px 12px 13px; }
        span.file_info {
          font-size: 11px;
          right: 46%; } }
      button {
        margin: 51px auto 0;
        display: flex; } } } }



@media screen and (max-width: 767px) {
  .sign_up {
    form {
      input.input_data {
        width: 100%; }
      select {
        width: 100%;
        margin-bottom: 2rem; }
      .mt {
        margin-top: 40px;
        .custom-file-upload {
          span.img_info {
            width: 301%;
            padding: 17px 230px 12px 13px; }
          span.file_info {
            font-size: 13px;
            right: 164%;
            font-weight: bold; } } } } } }

@media screen and (max-width: 575px) {
  .sign_up {
    form {
      .mt {
        .custom-file-upload {
          position: static;
          width: 30%;
          text-align: center;
          span.file_info {
            width: 90%;
            left: 6%;
            white-space: pre-line;
            text-align: left; }
          span.img_info {
            height: 57px;
            padding: 13px 0 12px;
            width: 70%;
            z-index: -1;
            top: 0;
            left: 15px; } } } } } }
@media screen and (max-width: 460px) {
  .sign_up {
    form {
      .custom-file-upload {
        padding: 13px 0 12px; } } } }


@media screen and (max-width: 436px) {
  .sign_up {
    form {
      span.after {
        left: 87%; } } } }
