/* user */
.user {
  display: flex;
  align-items: flex-start;
  margin-bottom: 102px;
  &_3,
  &_4,
  &_5 {
    margin-bottom: 50px; }
  img {
    width: 70px;
    height: 70px;
    border-radius: 50px;
    margin-left: 4px; }

  h3 {
    font-size: 22px;
    margin-top: 20px;
    margin-bottom: 3px;
    margin-left: 6px;
    width: 208px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    &:hover {
      .tool_tip {
        opacity: 1; } } }
  &_wrap {
    position: relative;
    p:nth-child(2) {
      margin-top: 6px;
      word-spacing: 0px; }

    p {
      margin-bottom: 0;
      margin-left: 7px; }
    .tool_tip {
      position: absolute;
      z-index: 9999;
      left: 36%;
      bottom: -17%;
      font-size: 10px;
      background: grey;
      padding: 5px;
      border-radius: 6px;
      opacity: 0; }
    p:nth-child(3) {
      width: 208px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      cursor: pointer;
      transition: .3s;
      &:hover {
        .tool_tip {
           opacity: 1; } } } } }


@media screen and (max-width: 991px) {
  .user {
    display: flex;
    flex-direction: column;
    align-items: center;
    &_wrap {
      text-align: center; } } }


@media screen and (max-width: 768px) {
  .user {
    &_wrap p:nth-child(3), h3 {
      overflow: auto;
      width: 100%;
      white-space: normal;
      font-size: 13px; }
    p:nth-child(3) {
      font-size: 12px; }
    &_wrap p {
        color: grey; }

    .tool_tip {
      display: none; } } }
