/* variables */

/* variables fonts */
$overFont: 'Overpass-SemiBold';
$SansFontReg: 'SourceSansPro-Regular';
$SansFontBold: 'SourceSansPro-Bold';

/* variables colors */
$primary: #EF6C00;
$second: #283593;
$back: #fff;


/* default styles */
body {
  font-family: $SansFontReg; }

h1,h2,h3,h4 {
  font-family: $overFont; }

h1 {
  font-size: 50px;
  line-height: 60px; }

h2 {
  font-size: 42px;
  line-height:  51px; }

h3 {
  font-size: 26px;
  line-height:  31px; }

h4 {
  font-size: 22px;
  line-height: 26px; }

h5 {
  font-family: $SansFontReg;
  font-size: 18px;
  line-height: 21px; }

@media screen and (max-width: 1024px) {
  h1 {
    font-size: 42px;
    line-height: 51px; } }

@media screen and (max-width: 768px) {
  h1 {
    font-size: 30px;
    line-height: 36px; }

  h2 {
     font-size: 26px;
     line-height: 31px; }

  h3 {
    font-size: 22px;
    line-height: 26px; }

  h5 {
    font-size: 16px;
    line-height: 20px; } }


.par1, .par2 , .par3 {
  font-family: $SansFontReg; }

.par1 {
  font-size: 18px;
  line-height: 26px; }

.par2 {
  font-size: 16px;
  line-height: 22px; }

.par3 {
  font-size: 14px;
  line-height: 20px; }


/* links */
a:hover {
  text-decoration: none;
  cursor: pointer;
  transition: .3s; }

.link_style_1 {
  color: #000;
  font-size: 16px;
  &:hover {
    color: $primary; } }

.link_style_2 {
  color: $back;
  font-size: 16px;
  &:hover {
    color: $primary; } }

.link_style_3 {
  color: $primary;
  font-family: $SansFontBold;
  font-size: 22px;
  &:hover {
    color: #FC831F; } }

.link_active {
  color: $second; }

/* icons */
.icon {
  &:hover {
    background: $primary; }
  &_active {
    background: $back;
    cursor: pointer; } }

/* buttons */
.button {
  font-family: $SansFontBold;
  font-size: 20px;
  padding: 6px 112px;
  border-radius: 10px;
  transition: .3s;
  cursor: pointer;
  outline: none;
  border: none; }

.button_frst {
  background: $primary;
  color: $back;
  &:hover {
    background: #FC831F; }
  &_disabled {
    background: #D7D7D7;
    color: #8D8C8C; } }

.button_scnd {
  border: 2px solid $primary;
  color: $primary;
  padding: 4px 93px;
  border-radius: 2px;
  &:hover {
    background: #FEE6D2; } }

@media screen and (max-width: 322px) {
  .button_frst {
    padding: 6px 111px; } }





