/* users */
.users {
  padding: 134px 200px;
  position: relative;
  h2 {
    padding-left: 3px;
    margin-bottom: 10px; }
  p {
    word-spacing: 2px;
    margin-left: 2px;
    margin-bottom: 57px; }
  button {
    background: 0;
    margin: 0 0 0 315px;
    outline: none; } }




@media screen and (max-width: 1312px) {
  .users {
    padding: 134px 100px; } }


@media screen and (max-width: 1150px) {
  .users {
    padding: 134px 0px; } }

@media screen and (max-width: 991px) {
  .users {
    button {
      display: flex;
      margin: auto; } } }
