/* contacts */
.contacts_wrap {
  padding: 15px 0 0 0;
  ul {
    list-style: none;
    color: #fff; }
  .contacts {
    padding: 8px 0 0 26px;
    li {
      display: flex;
      align-items: center;
      margin-bottom: 15px;
      img {
        margin-top: 3px; }
      p {
        margin-bottom: 0;
        margin-left: 9px;
        letter-spacing: 0.5px;
        font-size: 19px;
        font-family: 'SourceSansPro-Regular'; } }
    .contacts_element_2 {
      img {
        margin-top: 1px;
        margin-left: 2px; }
      p {
        margin-left: 10px; } }
    .contacts_element_3 {
      img {
        margin-top: 4px;
        margin-left: 4px; }
      p {
        margin-left: 13px; } } }
  .frst_links_footer,
  .scnd_links_footer,
  .thrd_links_footer,
  .frth_links_footer {
    text-align: right;
    margin-top: 9px;
    li {
      margin-bottom: 5px;
      a {
        color: #fff;
        &:hover {
          color: #EF6C00; } } } }
  .scnd_links_footer {
    margin-right: 8px; }
  .thrd_links_footer {
    margin-right: 16px; }
  .frth_links_footer {
    margin-right: 24px; } }


@media screen and (max-width: 991px) {
  .contacts_wrap {
    .contacts {
      li {
        img {
          display: none; }
        p {
          font-size: 15px; } } } } }


@media screen and (max-width: 767px) {
  .contacts_wrap {
    .contacts {
      padding-left: 0;
      li {
        justify-content: center; } }
    .frst_links_footer,
    .scnd_links_footer,
    .thrd_links_footer,
    .frth_links_footer {
      display: none; } } }


